const CustomHR = (props) => {

    const { begunModule, allModules, companyData, companyUsersArray, goto, components, authenticatedUser, user, companyProcesses, template } = props;
    const { title, selectedComponent, setSelectedComponent, step } = props;
    const [loading, setLoading] = useState(false);

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px' }}>
            {title}
        </div>
    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomHR);

export const CustomHRTimesheetStep = (props) => {
    const { selectedComponent, begunModule, goto, component_blueprint } = props;
    const { companyData, companyUsersArray, components } = useSelector(store => store.flowReducer);
    const { authenticatedUser } = useSelector(store => store.authReducer);

    const [loading, setLoading] = useState(false);
    const [items, setItems] = useState(selectedComponent?.component_items?.length > 0 ? selectedComponent.component_items.filter(it => format(it.value, 'MM/yyyy') === format((new Date()), 'MM/yyyy')) : [{ item_code: '', value: '', alt_value: '', id: generateNewUUID() }])

    const [filter, setFilter] = useState('month');
    const [end, setEnd] = useState(endOfWeek(new Date(), { weekStartsOn: 1 }));
    const [start, setStart] = useState(startOfWeek(end, { weekStartsOn: 1 }))
    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

    const addItemButton = () => (
        <Button
            light
            style={{ display: '' }}
            clicked={() => {
                setItems([...items, { item_code: '', value: '', alt_value: '', total: 0, quantity: '', id: generateNewUUID() }])
            }}
            icon={<img src={add} />}
        />
    );

    const DateInput = props => {

        return (

            <div style={{ width: '100%', height: '50px', border: '1px dashed gray', alignContent: 'center' }}>

                <DatePicker
                    selected={props?.value ? new Date(props?.value) : new Date()}
                    showTimeSelect={props.showTimeSelect ?? false}
                    timeFormat="HH:mm"
                    timeIntervals={15}
                    customInput={
                        <div
                            style={{
                                cursor: 'pointer',
                                display: 'flex',
                                justifyContent: 'center',
                                width: '100%',
                                alignItems: 'stretch',
                                padding: '5px',
                                border: '0px solid var(--app-color-gray-lighter)',
                            }}
                        >
                            <FontAwesomeIcon icon={faCalendarDays} style={{ margin: '0 10px', width: '20px', height: '20px' }} />
                            <input
                                value={props.value ? moment(props.value).format(props.format ?? 'DD MMMM YYYY HH:mm') : ''}
                                className={cls.inputValue}
                                type="text"
                                style={{
                                    flex: 1,
                                    marginTop: 'auto',
                                    marginBottom: 'auto',
                                    outline: 'none',
                                    fontFamily: 'var(--app-text-main-font)',
                                    fontSize: '16px',
                                    lineHeight: '20px',
                                    color: '#4A4A4A',
                                    textAlign: 'left',
                                    maxWidth: '98%',
                                    border: '0px'

                                }}
                                onChange={_e => { }}
                            />

                        </div>
                    }
                    className={cls.singleInputContainer}
                    onChange={date => {
                        props.handleDate(date);
                    }}
                />
            </div>
        )
    }

    const project = component_blueprint?.selected_fields?.find(sf => sf.data_type_name === 'Record' && sf.field_name === 'Project');

    return <div className={clsg.accordionContainer}>

        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px', padding: '20px' }}>

            <Tabs
                style={{ width: '100%', fontSize: isMobile ? '16px' : '20px', marginTop: '20px' }}
            >
                <TabList style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '14px' : '16px' }}>

                    <Tab>
                        {'BY TIME'}
                    </Tab>
                    <Tab onChange={() => {
                        setItems(selectedComponent.component_items)
                        setEnd(endOfWeek(new Date(), { weekStartsOn: 1 }));
                        setStart(startOfWeek(new Date(), { weekStartsOn: 1 }));
                    }}>
                        {'BY PROJECT'}
                    </Tab>

                </TabList>

                <TabPanel id="TIMESHEETS">
                    <div style={{ width: '100%', overflow: 'auto' }}>
                        <div style={{ display: 'flex', justifyContent: 'flex-end', width: '100%', padding: '20px' }}>

                            <div style={{ display: 'flex' }}>
                                <SelectAndMultiselect
                                    value={[{ label: 'Today', value: 'today' }, { label: 'This Week', value: 'week' }, { label: 'This Month', value: 'month' }, { label: 'Select Month', value: 'select' }, { label: 'Custom Date', value: 'custom' }].find(a => a.value === filter)?.label}
                                    className={cls.multiselectComponent}
                                    visible={true}
                                    mandatory={false}
                                    items={[{ label: 'Today', value: 'today' }, { label: 'This Week', value: 'week' }, { label: 'This Month', value: 'month' }, { label: 'Select Month', value: 'select' }, { label: 'Custom Date', value: 'custom' }]}
                                    onChange={(value) => {
                                        const filterDataToWeek = (data, formatting = 'd/MM/yyyy', currDay) => {
                                            const end = currDay ?? new Date();
                                            const start = startOfWeek(end, { weekStartsOn: 1 });
                                            const daysInWeekSoFarArr = eachDayOfInterval({ start, end }).map((day) =>
                                                format(day, formatting)
                                            );
                                            return data.filter((i) =>
                                                daysInWeekSoFarArr.includes(`${format(i.value, formatting)}`)
                                            );
                                        };
                                        if (value === 'week') {
                                            setItems(filterDataToWeek(selectedComponent.component_items));
                                        } else if (value === 'today') {
                                            const today = new Date();
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(today, 'd/MM/yyyy')))
                                        } else if (value === 'month') {
                                            const today = new Date();
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM/yyyy') === format(today, 'MM/yyyy')))
                                        } else if (value === 'custom') {
                                            setEnd(new Date());
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(new Date(), 'd/MM/yyyy')))
                                        }
                                        setFilter(value)
                                    }}
                                />
                                {filter === 'select' &&
                                    <SelectAndMultiselect
                                        titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                                        className={cls.multiselectComponent}
                                        visible={true}
                                        mandatory={false}
                                        items={[{ label: 'January', value: '01' }, { label: 'February', value: '02' }, { label: 'March', value: '03' },
                                        { label: 'April', value: '04' }, { label: 'May', value: '05' }, { label: 'June', value: '06' },
                                        { label: 'July', value: '07' }, { label: 'August', value: '08' }, { label: 'September', value: '09' },
                                        { label: 'October', value: '10' }, { label: 'November', value: '11' }, { label: 'December', value: '12' }]}
                                        onChange={(value) => {
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM') === value))
                                        }}
                                    />}
                                {filter === 'custom' &&
                                    <div style={{ display: 'flex', height: '50px', alignItems: 'center', width: '300px' }}>
                                        <DateInput value={end} style={{ width: '100%', border: '1px solid black' }}
                                            format={'DD MMMM YYYY'}
                                            handleDate={date => {
                                                setEnd(date);
                                                setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(date, 'd/MM/yyyy')))
                                            }}
                                        />

                                    </div>
                                }
                            </div>
                        </div>
                        {selectedComponent?.id ? <ModuleLists
                            className={cls.itemsTable}
                            columns={['Start Time', 'End Time', 'Work Details', 'Project', 'Duration', 'Options']}
                            modern={isMobile ? true : false}
                            list={R.sortWith([R.ascend(R.prop('value'))])(items)?.map(itm => {
                                return {
                                    ['Start Time']: <DateInput showTimeSelect value={itm.value} style={{ width: '100%', border: '1px solid black' }}
                                        handleDate={date => {
                                            itm.value = date;
                                            if (itm?.alt_value && itm?.value) {
                                                itm.total = (moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'minutes') / 60).toFixed(2);
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }
                                            setItems([...items.filter(it => it.id !== itm.id), itm]);
                                        }}
                                    />,
                                    ['End Time']: <DateInput showTimeSelect value={itm.alt_value} style={{ width: '100%', border: '1px solid black' }}
                                        handleDate={date => {
                                            itm.alt_value = date;

                                            if (itm?.alt_value && itm?.value) {
                                                itm.total = (moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'minutes') / 60).toFixed(2);
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }
                                            setItems([...items.filter(it => it.id !== itm.id), itm]);
                                        }}
                                    />,
                                    ['Duration']: <input
                                        value={itm?.total ?? 0.00}
                                        className={cls.inputValue}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />,
                                    ['Work Details']: <input defaultValue={itm.item_code}
                                        className={cls.inputValue}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: isMobile ? '100%' : '300px',
                                            paddingLeft: '10px'
                                        }}

                                        onBlur={(e) => {
                                            if (itm.item_code !== e.target.value) {
                                                itm.item_code = e.target.value
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }

                                        }}
                                    />,
                                    ['Project']: <div style={{ border: '1px dashed gray' }}>
                                        <SelectAndMultiselect
                                            value={itm?.quantity}
                                            titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                                            className={cls.dropdownComponent}
                                            visible={true}
                                            mandatory={false}


                                            items={components.filter(c => (c.component_fields.filter(cf => cf.component_blueprint_id === project?.options?.component_id).length > 0) || c.module_id === project?.options?.module_id).map(elem => {

                                                let label = '';
                                                project?.options?.names?.map(name => (label = label === '' ? elem.component_fields.find(cf => cf.field_name === name)?.value : `${label} ${elem.component_fields.find(cf => cf.field_name === name)?.value}`));
                                                label = (label === '' || !label) ? elem.title : label;

                                                return { label: label, value: label };
                                            })}




                                            onChange={(value) => {
                                                itm.quantity = value;
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }}
                                        />
                                    </div>,
                                    ['Options']: <FontAwesomeIcon icon={faTrash} onClick={() => {
                                        setTaskProcessContext({
                                            action: processTaskAction.setModuleItems,
                                            value: items.filter(it => it.id !== itm.id),
                                        });
                                        setItems(items.filter(it => it.id !== itm.id));
                                    }} />,
                                    id: itm.id
                                };
                            })}
                            footer={[<br key={0} />, <br key={1} />, <br key={2} />, <br key={3} />, <input key={4} readOnly className={cls.inputValue}
                                defaultValue={
                                    `${R.reduce((a, b) => +a + +b.total, 0, items ?? []).toFixed(2)}`} />, <br key={5} />]}
                        /> : props.children}
                        <div style={{ display: 'flex', justifyContent: 'left' }}>
                            {addItemButton()}
                        </div>
                    </div>
                </TabPanel>
                <TabPanel id="PROJECT">
                    <div style={{ width: '100%', overflow: 'auto' }}>
                        <div style={{ display: 'flex', justifyContent: 'flex-end', width: '100%', padding: '20px' }}>

                            <div style={{ display: 'flex', height: '50px', alignItems: 'center', width: '260px' }}>
                                <DateInput value={end} style={{ width: '100%', border: '1px solid black' }}
                                    format={'DD MMMM YYYY'}
                                    handleDate={date => {
                                        const new_end = endOfWeek(date, { weekStartsOn: 1 });
                                        setEnd(new_end);
                                        setStart(startOfWeek(new_end, { weekStartsOn: 1 }));
                                    }}
                                />

                            </div>
                        </div>
                        {selectedComponent?.id ? <ModuleLists
                            className={cls.itemsTable}
                            modern={isMobile ? true : false}
                            columns={['Project'].concat(
                                eachDayOfInterval({ start, end }).map((day) =>
                                    format(day, 'E MMM d')))
                            }
                            list={components.filter(c => (c.component_fields.filter(cf => cf.component_blueprint_id === project?.options?.component_id).length > 0) || c.module_id === project?.options?.module_id).map(elem => {
                                let label = '';
                                project?.options?.names?.map(name => (label = label === '' ? elem.component_fields.find(cf => cf.field_name === name)?.value : `${label} ${elem.component_fields.find(cf => cf.field_name === name)?.value}`));
                                label = (label === '' || !label) ? elem.title : label;
                                const object = {
                                    Project: <input defaultValue={label}
                                        className={cls.inputValue}
                                        style={{
                                            fontWeight: 'normal',
                                            width: isMobile ? '100%' : '300px'
                                        }}
                                        readOnly
                                    />
                                };
                                eachDayOfInterval({ start, end }).map((day) =>
                                    object[`${format(day, 'E MMM d')}`] = <input defaultValue={items.filter(it => it.quantity === label && `${format(it.value, 'E MMM d')}` === `${format(day, 'E MMM d')}`).map(it => it?.total).reduce((b, a) => b + +a, 0)}
                                        className={cls.inputValue}

                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '30px'
                                        }}
                                        onBlur={(e) => {
                                            const found = items.find(it => it.quantity === label && `${format(it?.value, 'E MMM d')}` === `${format(day, 'E MMM d')}`);
                                            if (found) {
                                                found.total = e.target.value;
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== found.id), found],
                                                });
                                                setItems([...selectedComponent.component_items.filter(i => i.id !== found.id), found])

                                            } else {
                                                const new_entry = {
                                                    value: moment(day).format('YYYY-MM-DD HH:mm:ss'),
                                                    total: e.target.value,
                                                    quantity: label,
                                                    id: generateNewUUID()
                                                }
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items, new_entry],
                                                });
                                                setItems([...selectedComponent.component_items, new_entry])
                                            }

                                        }}
                                    />
                                )

                                return object;
                            })}

                        /> : props.children}

                    </div>
                </TabPanel>
            </Tabs>
        </div>
    </div>;
}

export const CustomHRLeavesStep = (props) => {
    const { selectedComponent, begunModule, goto, component_blueprint } = props;
    const { companyData, companyUsersArray, components } = useSelector(store => store.flowReducer);
    const { authenticatedUser } = useSelector(store => store.authReducer);

    const [loading, setLoading] = useState(false);
    const [items, setItems] = useState(selectedComponent?.component_items?.length > 0 ? selectedComponent.component_items : [{ item_code: '', value: '', alt_value: '', id: generateNewUUID() }])

    const [filter, setFilter] = useState();
    const [end, setEnd] = useState(endOfWeek(new Date(), { weekStartsOn: 1 }));
    const [start, setStart] = useState(startOfWeek(end, { weekStartsOn: 1 }))
    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

    const addItemButton = () => (
        <Button
            light
            style={{ display: '' }}
            clicked={() => {
                setItems([...items, { item_code: '', value: '', alt_value: '', total: 0, quantity: '', id: generateNewUUID() }])
            }}
            icon={<img src={add} />}
        />
    );

    const DateInput = props => {

        return (

            <div style={{ width: '100%', height: '50px', border: '1px dashed gray', alignContent: 'center' }}>

                <DatePicker
                    selected={props?.value ? new Date(props?.value) : new Date()}
                    showTimeSelect={props.showTimeSelect ?? false}
                    timeFormat="HH:mm"
                    timeIntervals={15}
                    customInput={
                        <div
                            style={{
                                cursor: 'pointer',
                                display: 'flex',
                                justifyContent: 'center',
                                width: '100%',
                                alignItems: 'stretch',
                                padding: '5px',
                                border: '0px solid var(--app-color-gray-lighter)',
                            }}
                        >
                            <FontAwesomeIcon icon={faCalendarDays} style={{ margin: '0 10px', width: '20px', height: '20px' }} />
                            <input
                                value={props.value ? moment(props.value).format(props.format ?? 'DD MMMM YYYY HH:mm') : ''}
                                className={cls.inputValue}
                                type="text"
                                style={{
                                    flex: 1,
                                    marginTop: 'auto',
                                    marginBottom: 'auto',
                                    outline: 'none',
                                    fontFamily: 'var(--app-text-main-font)',
                                    fontSize: '16px',
                                    lineHeight: '20px',
                                    color: '#4A4A4A',
                                    textAlign: 'left',
                                    maxWidth: '98%',
                                    border: '0px'

                                }}
                                onChange={_e => { }}
                            />

                        </div>
                    }
                    className={cls.singleInputContainer}
                    onChange={date => {
                        props.handleDate(date);
                    }}
                />
            </div>
        )
    }

    const leaves = component_blueprint?.selected_fields?.find(sf => sf.data_type_name === 'Dropdown' && sf.field_name === 'Type');

    return <div className={clsg.accordionContainer}>
        <div style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '18px' : '20px', fontWeight: 'bold', marginTop: isMobile ? '' : '50px', padding: '20px' }}>

            <Tabs
                style={{ width: '100%', fontSize: isMobile ? '16px' : '20px', marginTop: '20px' }}
            >
                <TabList style={{ fontFamily: 'var(--app-text-mobile-font)', fontSize: isMobile ? '14px' : '16px' }}>

                    <Tab>
                        {'LEAVES'}
                    </Tab>
                    <Tab onChange={() => {
                        setItems(selectedComponent.component_items)
                        setEnd(endOfWeek(new Date(), { weekStartsOn: 1 }));
                        setStart(startOfWeek(new Date(), { weekStartsOn: 1 }));
                    }}>
                        {'BALANCES'}
                    </Tab>

                </TabList>

                <TabPanel id="LEAVES">
                    <div style={{ width: '100%', overflow: 'auto' }}>
                        <div style={{ display: 'flex', justifyContent: 'flex-end', width: '100%', padding: '20px' }}>

                            <div style={{ display: 'flex' }}>
                                <SelectAndMultiselect
                                    value={[{ label: 'Today', value: 'today' }, { label: 'This Week', value: 'week' }, { label: 'This Month', value: 'month' }, { label: 'Select Month', value: 'select' }].find(a => a.value === filter)?.label}
                                    className={cls.multiselectComponent}
                                    visible={true}
                                    mandatory={false}
                                    items={[{ label: 'Today', value: 'today' }, { label: 'This Week', value: 'week' }, { label: 'This Month', value: 'month' }, { label: 'Select Month', value: 'select' }]}
                                    onChange={(value) => {
                                        const filterDataToWeek = (data, formatting = 'd/MM/yyyy', currDay) => {
                                            const end = currDay ?? new Date();
                                            const start = startOfWeek(end, { weekStartsOn: 1 });
                                            const daysInWeekSoFarArr = eachDayOfInterval({ start, end }).map((day) =>
                                                format(day, formatting)
                                            );
                                            return data.filter((i) =>
                                                daysInWeekSoFarArr.includes(`${format(i.value, formatting)}`)
                                            );
                                        };
                                        if (value === 'week') {
                                            setItems(filterDataToWeek(selectedComponent.component_items));
                                        } else if (value === 'today') {
                                            const today = new Date();
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(today, 'd/MM/yyyy')))
                                        } else if (value === 'month') {
                                            const today = new Date();
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM/yyyy') === format(today, 'MM/yyyy')))
                                        } else if (value === 'custom') {
                                            setEnd(new Date());
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(new Date(), 'd/MM/yyyy')))
                                        }
                                        setFilter(value)
                                    }}
                                />
                                {filter === 'select' &&
                                    <SelectAndMultiselect
                                        titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                                        className={cls.multiselectComponent}
                                        visible={true}
                                        mandatory={false}
                                        items={[{ label: 'January', value: '01' }, { label: 'February', value: '02' }, { label: 'March', value: '03' },
                                        { label: 'April', value: '04' }, { label: 'May', value: '05' }, { label: 'June', value: '06' },
                                        { label: 'July', value: '07' }, { label: 'August', value: '08' }, { label: 'September', value: '09' },
                                        { label: 'October', value: '10' }, { label: 'November', value: '11' }, { label: 'December', value: '12' }]}
                                        onChange={(value) => {
                                            setItems(selectedComponent.component_items.filter(it => format(it.value, 'MM') === value))
                                        }}
                                    />}
                                {filter === 'custom' &&
                                    <div style={{ display: 'flex', height: '50px', alignItems: 'center', width: '300px' }}>
                                        <DateInput value={end} style={{ width: '100%', border: '1px solid black' }}
                                            format={'DD MMMM YYYY'}
                                            handleDate={date => {
                                                setEnd(date);
                                                setItems(selectedComponent.component_items.filter(it => format(it.value, 'd/MM/yyyy') === format(date, 'd/MM/yyyy')))
                                            }}
                                        />

                                    </div>
                                }
                            </div>
                        </div>
                        {selectedComponent?.id ? <ModuleLists
                            className={cls.itemsTable}
                            columns={['Date From', 'Date To', 'Details', 'Type', 'Duration', 'Options']}
                            list={R.sortWith([R.ascend(R.prop('value'))])(items)?.map(itm => {
                                return {
                                    ['Date From']: <DateInput showTimeSelect value={itm.value} style={{ width: '100%', border: '1px solid black' }}
                                        handleDate={date => {
                                            itm.value = date;
                                            if (itm?.alt_value && itm?.value) {
                                                itm.total = (moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'minutes') / 60).toFixed(2);
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }
                                            setItems([...items.filter(it => it.id !== itm.id), itm]);
                                        }}
                                    />,
                                    ['Date To']: <DateInput showTimeSelect value={itm.alt_value} style={{ width: '100%', border: '1px solid black' }}
                                        handleDate={date => {
                                            itm.alt_value = date;

                                            if (itm?.alt_value && itm?.value) {
                                                itm.total = (moment(new Date(itm.alt_value), 'DD/MM/YYYY').diff(moment(new Date(itm.value), 'DD/MM/YYYY'), 'minutes') / 60).toFixed(2);
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }
                                            setItems([...items.filter(it => it.id !== itm.id), itm]);
                                        }}
                                    />,
                                    ['Duration']: <input
                                        value={itm?.total ?? 0.00}
                                        className={cls.inputValue}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />,
                                    ['Details']: <input defaultValue={itm.item_code}
                                        className={cls.inputValue}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: '300px',
                                            paddingLeft: '10px'
                                        }}

                                        onBlur={(e) => {
                                            if (itm.item_code !== e.target.value) {
                                                itm.item_code = e.target.value
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }

                                        }}
                                    />,
                                    ['Type']: <div style={{ border: '1px dashed gray', width: '300px' }}>
                                        <SelectAndMultiselect
                                            value={itm?.type}
                                            titleProps={{ fontSize: '12px', color: 'var(--app-color-gray-light)' }}
                                            className={cls.dropdownComponent}
                                            visible={true}
                                            mandatory={false}
                                            items={leaves.options?.list.map(li => {
                                                return { label: li, value: li }
                                            })}
                                            onChange={(value) => {
                                                itm.type = value;
                                                setTaskProcessContext({
                                                    action: processTaskAction.setModuleItems,
                                                    value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                                });
                                            }}
                                        />
                                    </div>,
                                    ['Options']: <div style={{ display: 'flex', justifyContent: 'space-evenly' }}>
                                        <FontAwesomeIcon icon={faThumbsUp} onClick={() => {
                                            itm.quantity = 1;
                                            setTaskProcessContext({
                                                action: processTaskAction.setModuleItems,
                                                value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                            });
                                            setItems([...items.filter(i => i.id !== itm.id), itm]);
                                        }}
                                            style={{ color: 'green', cursor: 'pointer', pointerEvents: +itm.quantity === 1 ?'none':'' }}
                                        />
                                        <FontAwesomeIcon icon={faUserXmark} onClick={() => {
                                            itm.quantity = 0;
                                            setTaskProcessContext({
                                                action: processTaskAction.setModuleItems,
                                                value: [...selectedComponent.component_items.filter(i => i.id !== itm.id), itm],
                                            });
                                            setItems([...items.filter(i => i.id !== itm.id), itm]);
                                        }}
                                            style={{ color: 'red', cursor: 'pointer', pointerEvents: +itm.quantity === 0 ?'none':'' }}
                                        />
                                        <FontAwesomeIcon icon={faTrash} onClick={() => {
                                            setTaskProcessContext({
                                                action: processTaskAction.setModuleItems,
                                                value: items.filter(it => it.id !== itm.id),
                                            });
                                            setItems(items.filter(it => it.id !== itm.id));
                                        }}
                                            style={{ color: 'black', cursor: 'pointer' }}
                                        />
                                    </div>,

                                    id: itm.id
                                };
                            })}

                        /> : props.children}
                        <div style={{ display: 'flex', justifyContent: 'left' }}>
                            {addItemButton()}
                        </div>
                    </div>
                </TabPanel>
                <TabPanel id="DETAILS">
                    <div style={{ width: '100%', overflow: 'auto' }}>

                        {selectedComponent?.id ? <ModuleLists
                            className={cls.itemsTable}
                            columns={['Type', 'Allocated', 'Availed', 'Balance']}
                            list={leaves.options?.list.map(li => {
                                return {
                                    Type: <input
                                        className={cls.inputValue}
                                        defaultValue={li}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: '100%',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />,
                                    Allocated: <input
                                        className={cls.inputValue}
                                        defaultValue={selectedComponent.component_fields.find(cf => cf.field_name === li)?.value ?? '0'}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: '100%',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />,
                                    Availed: <input
                                        className={cls.inputValue}
                                        defaultValue={`${R.reduce((a, b) => +a + +b.total, 0, selectedComponent.component_items.filter(it => it.type === li && +it.quantity === 1) ?? []).toFixed(2)}`}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: '100%',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />,
                                    Balance: <input
                                        className={cls.inputValue}
                                        defaultValue={`${(+(selectedComponent.component_fields.find(cf => cf.field_name === li)?.value ?? 0) -
                                            R.reduce((a, b) => +a + +b.total, 0, selectedComponent.component_items.filter(it => it.type === li && +it.quantity === 1) ?? [])).toFixed(2)}`}
                                        style={{
                                            fontWeight: 'normal',
                                            border: '1px dashed gray',
                                            height: '50px',
                                            width: '100%',
                                            paddingLeft: '10px'
                                        }}
                                        readOnly
                                    />
                                }
                            })}

                        /> : props.children}

                    </div>
                </TabPanel>
            </Tabs>
        </div>
    </div>;
}